@import "../../scss/mixins.scss";

.sideBarWrapper {
  font-family: "HelveticaNeueCyr-Medium";
  background: #ffffff;
  box-shadow: 0px 1px 75px rgba(90, 97, 105, 0.11),
    0px 2px 4px rgba(90, 97, 105, 0.12), 0px 7.5px 11px rgba(90, 97, 105, 0.1),
    0px 3.5px 17.5px rgba(165, 182, 201, 0.1);
  width: 20%;
}

.siderBarLogoWrapper {
  justify-content: center;
  @include rowCenterFlex();
  height: 68px;
  // padding: 24px 116px 24px 46px;
  .logoText {
    font-size: 16px;
    margin-left: 8px;
    @include greyBlueText();
  }
}

.linksListWrapper {
  ul {
    .currentLi {
      background: #fbfbfb;
      box-shadow: inset 0px -1px 0px #e1e5eb, inset 4px 0px 0px #007bff;
      .linkName {
        @include blue();
        &:hover {
          @include blue();
        }
      }
    }
    li {
      list-style: none;
      box-shadow: inset 0px -1px 0px #e1e5eb;
      // height: 58px;
      a {
        @include rowCenterFlex();
        padding: 20px 10px 20px 10px;
        .iconWrapper {
          width: 14px;
          height: 14px;
        }
        .linkName {
          font-size: 15px;
          @include greyBlueText();
          margin-left: 12px;
          &:hover {
            @include blue();
          }
        }
      }
    }
  }
}

.sideBarBtn {
  display: none;
}

@media screen and (max-width: 640px) {
  .sideBarWrapper {
    display: none;
  }
  .sideBarBtn {
    display: inline-block;
    background: #007bff;
  }
}

@media screen and (min-width: 1440px) {
  .siderBarLogoWrapper {
    padding: 24px 116px 24px 46px;
  }
  .linksListWrapper {
    ul {
      li {
        a {
          padding: 20px 32px 20px 26px;
        }
      }
    }
  }
}
